<template>
    <div class="container ">
       <v-card max-width="100%">
        <div class="menu">
	<div class="card">
		<div class="card-header">
			<h2>Actions</h2>
		</div>
		<div class="card-content">
			<router-link to="/create" class="link">Create Posts</router-link>
			<router-link :to=" '../update/' + post._id " class="link" v-if = "post.author == user.username">Update Post</router-link>
			<v-btn flat class="link" @click="displaydialog = !displaydialog" >Comment</v-btn>
		</div>
	</div>
</div>
</v-card>
      <!-- post detail -->
       <div class="main">
    <v-card tile class="post mx-auto"  max-width="700px" >
       <img :src="post.image" alt="">
      <v-card-item>
        <h3>{{post.title}}</h3>
       
        <p>{{post.description}}</p>
      </v-card-item>
      <v-divider></v-divider>
      <div class="actions">
        <h4>Author: <span>{{post.author}}</span> </h4>
        <v-spacer></v-spacer>
        <v-btn @click="blogLike"><v-icon>mdi-charity </v-icon> {{count}}</v-btn>
        <v-btn @click="displaydialog = !displaydialog"><v-icon>mdi-comment </v-icon>comment</v-btn>
      </div>
     
    </v-card >
    <v-card class="comment mx-auto "  max-width="700px" v-for="comment in post.comments" :key="comment._id">
      <v-card-title>
        <h4 class="headline text-black px-2">{{comment.name}}</h4>
        <v-spacer></v-spacer>
      <v-span>{{comment.date}}</v-span>
      </v-card-title>
      <p class="px-2">{{comment.content}}</p>
    </v-card>
    <!-- comment section -->
     <v-dialog v-model="displaydialog" persistent width="100%">
    <v-card class="mx-auto" max-width="1080px" >
      <v-card-item>
        <v-card-title> Comment</v-card-title>
        <v-form @submit = "submitComment" ref="form" >
          <v-text-field
          v-model="user.username"
          label="name"
          />
          
          <v-textarea 
          v-model="comment.content"
          label="comment"
          />
          <v-card-action>
            <v-btn @click = "displaydialog = !displaydialog">close</v-btn>
           <v-btn type="submit">post</v-btn>
          </v-card-action>
          
        </v-form>
      </v-card-item>
    </v-card>
    </v-dialog>
   </div>
   <v-snackbar top timeout="300" color="green" v-model="snack">
      {{success}}
   </v-snackbar>
     </div>
</template>

<script>
import { defineComponent} from 'vue';
import axios from 'axios';
import { mapGetters,mapActions} from 'vuex';


export default  defineComponent ({
    name:'singleView',
   
    component:{
       
    },
    data:()=>({
      snack:false,
      displaydialog:false,
       post:[],
       count:'',
       comment: {
        name:'',
        content:''
       },
       success:''
       
    }),
    computed:{
         ...mapGetters(['isLoggedIn']),
          ...mapGetters(['user'])
    },
    methods:{
      ...mapActions(['getProfile']),
      ...mapActions(['createComment']),
       ...mapActions(['BlogLike']),
      //comment method
      async submitComment(){
           let comment={
              id:this.$route.params.id,
              name: this.user.username,
              content :this.comment.content,
           };
          this.createComment(comment).then(res=>{
             if(res.data.success){
              this.success = res.data.msg
              this.snack=true
             }
          }).catch(err=>{
            console.log(err)
           })
        
        
       },
         async blogLike(){
           let bloglike={
               id:this.$route.params.id,
              post:this.post._id,
              user: this.post.author,
           };
          this.BlogLike(bloglike).then(res=>{
            if(res.data.success){
              this.success = res.data.msg
                 this.snack=true
            }
          }).catch(err=>{
            console.log(err)
           })
          
         
       },
        
             
       
      
    },
     async  mounted(){
              this.getProfile()
            let res = await axios.get('http://localhost:5000/api/posts/' + this.$route.params.id)
             this.post = res.data.result;
             this.count=this.post.likes.length
        
        } 
})



</script>

<style scoped>

.container{
  display: flex;
}
 .container.menu .card .card-header {
	padding: 10px;
	text-align: center;
	border-radius: 3px 3px 0px 0px;
	background: #3E606F;
}
.container.menu .card .card-header h2 { color: white; }
.container .menu .card .card-content .link {
	display: block;
	box-sizing: border-box;
	padding: 8px 10px;
	border-bottom: 1px solid #e4e1e1;
	color: #444;
}
.container.menu .card .card-content .link:hover {
	padding-left: 20px; background: #F9F9F9; transition: 0.1s;
}

.actions {
  display: flex;
  
}
.card-header h2{
  border-bottom: 6px solid crimson;
}
.card-content .link{
  text-decoration: none;
}
.card-content .link:hover{
  background: crimson;
}

.comment{
 margin-top: 40px;
 border-left: 6px solid crimson;
}
.post {
  margin: 0 0 0;
  padding-left:30px;
  border-left: 6px solid crimson;
  border-right: 6px solid crimson;
}
@media screen and (max-width: 900px) {
  v-card{
     max-width: 100%;
  }
.actions {
  display: flex;
  width: 0%;
}
.container{
  display: block;
}
.card-content{
  display: flex;
}
v-card-action{
  margin-right: 0;
}
}
</style>